import style from './Post.module.css'
import {PostType} from "../../../../Redux/profileReducer";



export const Post = (props: PostType) => {
    return (
        <div className={style.item}>
            <div>
                <img
                    src='data:image/jpeg;base64,'/>
                {props.postText}
            </div>
            <div>
                <span>Like</span>{props.likesCount}
            </div>
        </div>
    )
}

